import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { PLsinger } from 'src/app/shared_models/singer.model';
import { PLsong } from 'src/app/shared_models/song.model';
import { DataManagerService } from 'src/app/shared_services/data-manager.service';
import { SongComponent } from '../additional_components/song/song.component';
import { NgIf, NgFor } from '@angular/common';

@Component({
  selector: 'app-artist-page',
  templateUrl: './artist-page.component.html',
  styleUrls: ['./artist-page.component.css'],
  standalone: true,
  imports: [NgIf, NgFor, SongComponent],
})
export class ArtistPageComponent implements OnInit {
  singerId: any;
  singer!: PLsinger | undefined;
  singerSongs!: PLsong[];
  singers!: PLsinger[];
  songs!: PLsong[];

  constructor(
    private router: ActivatedRoute,
    private manager: DataManagerService
  ) {}

  ngOnInit(): void {
    this.router.paramMap.subscribe(async (params) => {
      this.singerId = params.get('id');

      try {
        this.songs = await this.manager.getSongs();
        this.singers = await this.manager.getSingers();

        if (this.singerId) {
          const _singer = this.singers.filter((s) => s.id == this.singerId);
          if (_singer.length > 0) {
            this.singer = _singer[0];
            this.singerSongs = this.songs.filter(
              (s) => s.singer === _singer[0].singer
            );
          }
        }
      } catch (error) {
        console.error('Error fetching data', error);
      }
    });
  }
}
