import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Album } from 'src/app/shared_models/album.model';
import { PLsong } from 'src/app/shared_models/song.model';
import { DataManagerService } from 'src/app/shared_services/data-manager.service';
import { SongComponent } from '../additional_components/song/song.component';
import { NgFor } from '@angular/common';

@Component({
  selector: 'app-album-page',
  templateUrl: './album-page.component.html',
  styleUrls: ['./album-page.component.css'],
  standalone: true,
  imports: [NgFor, SongComponent],
})
export class AlbumPageComponent implements OnInit {
  songs!: PLsong[];

  albumName!: string | null;
  albumCover!: string;
  albums!: Album[];
  albumSongs!: PLsong[];

  constructor(
    private router: ActivatedRoute,
    private manager: DataManagerService
  ) {}

  ngOnInit(): void {
    this.router.paramMap.subscribe(async (params) => {
      this.albumName = params.get('album');

      try {
        this.songs = await this.manager.getSongs();
        this.albumSongs = this.songs.filter((s) => s.album === this.albumName);

        this.albums = await this.manager.getAlbums();
        const album = this.albums.find((s) => s.album === this.albumName);

        if (album) {
          this.albumCover = album.albumCover;
        }
      } catch (error) {
        console.error('Error fetching data', error);
      }
    });
  }
}
