import { Component, OnInit } from '@angular/core';
import { PLsong } from 'src/app/shared_models/song.model';
import { DataManagerService } from 'src/app/shared_services/data-manager.service';
import { TranslateModule } from '@ngx-translate/core';
import { SongCubeComponent } from '../additional_components/song-cube/song-cube.component';
import { NgFor, NgIf, SlicePipe } from '@angular/common';

@Component({
  selector: 'app-recent',
  templateUrl: './recent.component.html',
  styleUrls: ['./recent.component.css'],
  standalone: true,
  imports: [NgFor, SongCubeComponent, NgIf, SlicePipe, TranslateModule],
})
export class RecentComponent implements OnInit {
  recentSongs: PLsong[] = [];
  GlobalStart: number = 0;
  GlobalEnd: number = 16;

  constructor(private manager: DataManagerService) {}

  async ngOnInit(): Promise<void> {
    try {
      this.recentSongs = await this.manager.getRecentSongs();
    } catch (error) {
      console.error('Error fetching recent songs', error);
    }
  }

  goTo(where: string) {
    if (where == 'Back') {
      this.GlobalStart = this.GlobalStart - 16;
      this.GlobalEnd = this.GlobalEnd - 16;
    } else {
      this.GlobalStart = this.GlobalStart + 16;
      this.GlobalEnd = this.GlobalEnd + 16;
    }
  }
}
