import { Component, OnInit } from '@angular/core';
import { PLsong } from 'src/app/shared_models/song.model';
import { DataManagerService } from 'src/app/shared_services/data-manager.service';
import { TranslateModule } from '@ngx-translate/core';
import { SongCubeComponent } from '../additional_components/song-cube/song-cube.component';
import { NgFor } from '@angular/common';

@Component({
  selector: 'app-suggestions',
  templateUrl: './suggestions.component.html',
  styleUrls: ['./suggestions.component.css'],
  standalone: true,
  imports: [NgFor, SongCubeComponent, TranslateModule],
})
export class SuggestionsComponent implements OnInit {
  songs!: PLsong[];

  constructor(private manager: DataManagerService) {}

  async ngOnInit(): Promise<void> {
    try {
      this.songs = await this.manager.getShuffledSongs(16);
    } catch (error) {
      console.error('Error fetching shuffled songs', error);
    }
  }
}
