import { Component, OnInit } from '@angular/core';
import { PLsinger } from 'src/app/shared_models/singer.model';
import { PLsong } from 'src/app/shared_models/song.model';
import { DataManagerService } from 'src/app/shared_services/data-manager.service';
import { TranslateModule } from '@ngx-translate/core';
import { SongComponent } from '../additional_components/song/song.component';
import { ArtistComponent } from '../additional_components/artist/artist.component';
import { NgIf, NgFor } from '@angular/common';

@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.css'],
  standalone: true,
  imports: [NgIf, NgFor, ArtistComponent, SongComponent, TranslateModule],
})
export class SearchComponent implements OnInit {
  songResults: PLsong[] | undefined = undefined;
  singerResults: PLsinger[] | undefined = undefined;
  songs!: PLsong[];
  singers!: PLsinger[];

  constructor(private manager: DataManagerService) {}

  async ngOnInit(): Promise<void> {
    try {
      this.songs = await this.manager.getSongs();
      this.singers = await this.manager.getSingers();
    } catch (error) {
      console.error('Error fetching songs or singers', error);
    }
  }

  Search(event: Event) {
    const newSong = (event.target as HTMLInputElement).value;
    let SingersFilter = this.singers.filter(
      (value) =>
        value.singer.toLowerCase().slice(0, newSong.length) ===
        newSong.toLowerCase()
    );
    let SongsFilter = this.songs.filter(
      (value) =>
        value.songtitle.toLowerCase().slice(0, newSong.length) ===
        newSong.toLowerCase()
    );
    if (newSong == '') {
      this.singerResults = undefined;
      this.songResults = undefined;
      SingersFilter = [];
      SongsFilter = [];
    } else {
      this.singerResults = SingersFilter;
      this.songResults = SongsFilter;
    }
  }
}
