import { Component, OnInit } from '@angular/core';
import { PLsinger } from 'src/app/shared_models/singer.model';
import { DataManagerService } from 'src/app/shared_services/data-manager.service';
import { TranslateModule } from '@ngx-translate/core';
import { ArtistComponent } from '../additional_components/artist/artist.component';
import { NgFor } from '@angular/common';

@Component({
    selector: 'app-artists',
    templateUrl: './artists.component.html',
    styleUrls: ['./artists.component.css'],
    standalone: true,
    imports: [NgFor, ArtistComponent, TranslateModule]
})
export class ArtistsComponent implements OnInit {

  singers:PLsinger [] = [];

  constructor(private manager:DataManagerService) { }

  async ngOnInit(): Promise<void> {
    this.singers = await this.manager.getSingers();
  }

}
