import { Component, OnInit } from '@angular/core';
import { PLsong } from 'src/app/shared_models/song.model';
import { DataManagerService } from 'src/app/shared_services/data-manager.service';
import { TranslateModule } from '@ngx-translate/core';
import { SongComponent } from '../additional_components/song/song.component';
import { NgFor, NgIf, SlicePipe } from '@angular/common';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css'],
  standalone: true,
  imports: [NgFor, SongComponent, NgIf, SlicePipe, TranslateModule],
})
export class ProfileComponent implements OnInit {
  songs!: PLsong[];

  navStart: number = 0;
  navEnd: number = 10;

  constructor(private manager: DataManagerService) {}

  async ngOnInit(): Promise<void> {
    this.songs = await this.manager.getSongs();
  }

  goBack() {
    this.navStart = this.navStart - 10;
    this.navEnd = this.navEnd - 10;
  }

  goNext() {
    this.navStart = this.navStart + 10;
    this.navEnd = this.navEnd + 10;
  }
}
