import {
  AfterViewInit,
  Component,
  ElementRef,
  HostListener,
  OnInit,
  ViewChild,
} from '@angular/core';
import {
  Router,
  RouterLink,
  RouterLinkActive,
  RouterOutlet,
} from '@angular/router';
import { TranslateService, TranslateModule } from '@ngx-translate/core';
import { BehaviorSubject, Subject, Subscription } from 'rxjs';
import { Playlist } from './shared_models/playlist.model';
import { PLsinger } from './shared_models/singer.model';
import { PLsong } from './shared_models/song.model';
import { DarkModeService } from './shared_services/dark-mode.service';
import { DataManagerService } from './shared_services/data-manager.service';
import { StatusExchangerService } from './shared_services/status-exchanger.service';
import { FormsModule } from '@angular/forms';
import { NgFor, NgIf, NgStyle, AsyncPipe, SlicePipe } from '@angular/common';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
  standalone: true,
  providers: [DataManagerService],
  imports: [
    RouterLink,
    RouterLinkActive,
    RouterOutlet,
    NgFor,
    NgIf,
    NgStyle,
    FormsModule,
    AsyncPipe,
    SlicePipe,
    TranslateModule,
  ],
})
export class AppComponent implements OnInit, AfterViewInit {
  title = 'Feelife';
  mobileNavStatus = '';

  currentProgress$ = new BehaviorSubject<number>(0);
  currentTime$ = new Subject<string>();

  @ViewChild('player', { static: true }) player!: ElementRef<HTMLAudioElement>;
  @ViewChild('volumeSlider', { static: true })
  volumeSlider!: ElementRef<HTMLInputElement>;

  songs: PLsong[] = [];
  singers: PLsinger[] = [];
  playlists: Playlist[] = [];
  favoritedSongs: PLsong[] = [];
  innerWidth: number = window.innerWidth;

  audio = new Audio();
  isPlaying = false;
  activeSong?: PLsong;
  durationTime?: string;
  songCoverImage: string = '';

  paramsSubscription: Subscription | null = null;

  language: string = localStorage.getItem('language') ?? 'en';

  repeatStatus = false;
  shuffleStatus = false;
  vol = 100;

  constructor(
    private statusExchanger: StatusExchangerService,
    private dataManager: DataManagerService,
    public router: Router,
    private translate: TranslateService,
    private darkMode: DarkModeService
  ) {
    this.translate.setDefaultLang(this.language);
    this.translate.use(this.language);
  }

  ngOnInit() {
    this.loadData();
    this.initSubscriptions();
  }

  ngAfterViewInit() {
    this.changeVolume();
  }

  @HostListener('window:resize')
  onResize() {
    this.innerWidth = window.innerWidth;
  }

  private loadData() {
    try {
      this.dataManager.getSongs().then((songs) => {
        this.songs = songs;
        if (this.songs.length > 0) {
          this.chooseSong(this.songs[0]);
        }
      });

      this.dataManager.getSingers().then((singers) => {
        this.singers = singers;
      });
      this.dataManager.getPlaylists().then((playlists) => {
        this.playlists = playlists;
      });
    } catch (error) {
      console.error('Error fetching data', error);
    }
  }

  private initSubscriptions() {
    this.statusExchanger.activeLanguageCode.subscribe((code: string) => {
      this.setLanguage(code);
    });

    this.statusExchanger.activeSongId.subscribe((songId: number) => {
      this.playSongById(songId);
    });
  }

  private setLanguage(code: string) {
    this.language = code;
    localStorage.setItem('language', code);
    this.translate.setDefaultLang(code);
    this.translate.use(code);
  }

  private playSongById(songId: number) {
    const song = this.songs.find((s) => s.id === songId) || this.songs[0];
    this.chooseSong(song);
    this.onPlay();
  }

  onPlay() {
    if (this.activeSong == undefined) {
      this.chooseSong(this.songs[0]);
    }

    this.audio.play();
    this.isPlaying = true;
    this.durationTime = undefined;

    this.audio.ontimeupdate = () => this.onTimeUpdate();
    this.audio.onended = () => this.playNextSong();
  }

  chooseSong(song: PLsong) {
    this.audio.pause();
    this.audio.src = song.path;
    this.activeSong = song;
    this.dataManager.addRecentSong(song);
  }

  private onTimeUpdate() {
    if (!this.durationTime) {
      this.setSongDuration();
    }

    const currentTime = this.audio.currentTime;
    const duration = this.audio.duration;

    const currentMinutes = Math.floor(currentTime / 60);
    const currentSeconds = ('0' + Math.floor(currentTime % 60)).slice(-2);
    this.currentTime$.next(`${currentMinutes}:${currentSeconds}`);

    const percentage = Math.round((currentTime / duration) * 100);
    this.currentProgress$.next(percentage);
  }

  private setSongDuration() {
    const duration = this.audio.duration;
    const minutes = Math.floor(duration / 60);
    const seconds = ('0' + Math.floor(duration % 60)).slice(-2);
    this.durationTime = `${minutes}:${seconds}`;
  }

  playNextSong() {
    const currentIndex = this.songs.findIndex(
      (s) => s.id === this.activeSong?.id
    );
    const nextIndex = (currentIndex + 1) % this.songs.length;
    this.chooseSong(this.songs[nextIndex]);
    this.onPlay();
  }

  playPreviousSong() {
    const currentIndex = this.songs.findIndex(
      (s) => s.id === this.activeSong?.id
    );
    const prevIndex =
      (currentIndex - 1 + this.songs.length) % this.songs.length;
    this.chooseSong(this.songs[prevIndex]);
    this.onPlay();
  }

  onPause() {
    this.audio.pause();
    this.isPlaying = false;
    this.currentProgress$.next(0);
    this.currentTime$.next('0:00');
    this.durationTime = undefined;
  }

  changeVolume() {
    this.audio.volume = this.vol / 100;
    this.volumeSlider.nativeElement.style.background = `linear-gradient(to right, #82CFD0 0%, #82CFD0 ${this.vol}%, #fff ${this.vol}%, #fff 100%)`;
  }

  toggleRepeat() {
    this.repeatStatus = !this.repeatStatus;
  }

  toggleShuffle() {
    this.shuffleStatus = !this.shuffleStatus;
    this.statusExchanger.shuffleStatus.emit(this.shuffleStatus);
  }
}
