import { Component, OnInit } from '@angular/core';
import { Album } from 'src/app/shared_models/album.model';
import { DataManagerService } from 'src/app/shared_services/data-manager.service';
import { TranslateModule } from '@ngx-translate/core';
import { AlbumCubeComponent } from '../additional_components/album-cube/album-cube.component';
import { NgFor, NgStyle } from '@angular/common';

@Component({
  selector: 'app-albumbs',
  templateUrl: './albums.component.html',
  styleUrls: ['./albums.component.css'],
  standalone: true,
  imports: [NgFor, AlbumCubeComponent, NgStyle, TranslateModule],
})
export class AlbumsComponent implements OnInit {
  albums!: Album[];

  constructor(private manager: DataManagerService) {}

  async ngOnInit(): Promise<void> {
    this.albums = await this.manager.getAlbums();
  }
}
