import { Component, OnInit } from '@angular/core';
import { PLsong } from 'src/app/shared_models/song.model';
import { DataManagerService } from 'src/app/shared_services/data-manager.service';
import { TranslateModule } from '@ngx-translate/core';
import { SongComponent } from '../additional_components/song/song.component';
import { SongCubeComponent } from '../additional_components/song-cube/song-cube.component';
import { NgFor, NgIf, SlicePipe } from '@angular/common';
import { RouterLink } from '@angular/router';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css'],
  standalone: true,
  imports: [
    RouterLink,
    NgFor,
    SongCubeComponent,
    NgIf,
    SongComponent,
    SlicePipe,
    TranslateModule,
  ],
})
export class HomeComponent implements OnInit {
  top50songs: PLsong[] = [];
  recentSongs: PLsong[] = [];
  recommendedSongs: PLsong[] = [];

  constructor(private manager: DataManagerService) {}

  async ngOnInit(): Promise<void> {
    try {
      this.recentSongs = await this.manager.getRecentSongs();
      this.top50songs = await this.manager.getShuffledSongs(50);
      this.recommendedSongs = await this.manager.getShuffledSongs(50);
    } catch (error) {
      console.error('Error fetching data', error);
    }
  }
}
