import { Injectable } from '@angular/core';
import { Album } from '../shared_models/album.model';
import { PLsinger } from '../shared_models/singer.model';
import { PLsong } from '../shared_models/song.model';
import { Playlist } from '../shared_models/playlist.model';

import { HttpClient } from '@angular/common/http';
import { lastValueFrom } from 'rxjs/internal/lastValueFrom';

@Injectable()
export class DataManagerService {
  favoritedSongs: PLsong[] = [];
  recentSongs: PLsong[] = [];
  private readonly apiUrl =
    'https://raw.githubusercontent.com/gokadzev/fake-apis-for-projects/main/feelife/mmpwa.json';
  private readonly playlistsApiUrl =
    'https://raw.githubusercontent.com/gokadzev/fake-apis-for-projects/main/feelife/mmplaylists.json';

  constructor(private http: HttpClient) {}

  private updateLocalStorage(key: string, data: any[]): void {
    localStorage.setItem(key, JSON.stringify(data));
  }

  private getLocalStorage(key: string): any[] {
    return JSON.parse(localStorage.getItem(key) || '[]');
  }

  addRecentSong(song: PLsong): void {
    if (!this.recentSongs.some((s) => s.id === song.id)) {
      this.recentSongs.unshift(song);
      const recentSongsFromLocalStorage = this.getLocalStorage('recentSongs');
      recentSongsFromLocalStorage.push(song.id);
      this.updateLocalStorage('recentSongs', recentSongsFromLocalStorage);

      if (this.recentSongs.length > 49) {
        this.recentSongs.pop();
      }
    }
  }

  async getRecentSongs(): Promise<PLsong[]> {
    const recentSongsFromLocalStorage = this.getLocalStorage('recentSongs');

    if (recentSongsFromLocalStorage.length !== this.recentSongs.length) {
      const songs = await this.getSongs();
      const mappedSongs = recentSongsFromLocalStorage.map((id: number) =>
        songs.find((s: PLsong) => s.id === id)
      );

      this.recentSongs = (mappedSongs || []) as PLsong[];
    }
    return this.recentSongs;
  }

  getSongs(): Promise<PLsong[]> {
    return lastValueFrom(this.http.get<PLsong[]>(this.apiUrl))
      .then((response) => response)
      .catch((error) => {
        console.error('Error fetching songs', error);
        return [];
      });
  }

  getSingers(): Promise<PLsinger[]> {
    return this.getSongs().then((songs) => {
      const singers: PLsinger[] = [];
      let singer = '';
      songs.forEach((song, index) => {
        if (singer !== song.coverphoto) {
          singers.push(new PLsinger(index, song.singer, song.coverphoto));
          singer = song.coverphoto;
        }
      });
      return singers;
    });
  }

  getAlbums(): Promise<Album[]> {
    return this.getSongs().then((songs) => {
      const albums: Album[] = [];
      songs.forEach((song) => {
        const existingAlbum = albums.find((album) => album.album === song.album);
        if (!existingAlbum) {
          albums.push(new Album(song.album, song.albumCover));
        }
      });
      return albums;
    });
  }

  getPlaylists(): Promise<Playlist[]> {
    return lastValueFrom(this.http.get<Playlist[]>(this.playlistsApiUrl))
      .then((response) => response)
      .catch((error) => {
        console.error('Error fetching playlists', error);
        return [];
      });
  }

  getShuffledSongs(number: number | 'all'): Promise<PLsong[]> {
    return this.getSongs().then((songs) => {
      const shuffledArray: PLsong[] = [];
      let songsList: PLsong[] = songs.slice(); // Create a copy of the songs list to shuffle.

      if (number === 'all') {
        number = songsList.length;
      }

      for (let i = 0; i < number; i++) {
        const randIndex = Math.floor(Math.random() * songsList.length);
        shuffledArray.push(songsList[randIndex]);
        songsList.splice(randIndex, 1); // Remove the selected song from the list.
      }

      return shuffledArray;
    });
  }
}

